    font-style: normal;
}


:root {
    /* Light default */
    --default-color: #403733;
    --highlight: #79A9AE;
    --secondary-highlight: #e98074;
    --background: #eae7dc;
    --secondary-background: #d8c3a5;
    --tertiary-background: #C5B092;

