        color-scheme: dark;
        --default-color: #fafafa;
        --highlight: #D26140;
        --secondary-highlight: #86639E;
        --background: #000317;
        --secondary-background: #161B33;
    } */
}

/* Abandoned this way because of lack of safari support */
  /* :root {
    color-scheme: light dark;
